import React from "react";
import './NavMenuButton.scss';

export const NavMenuButton = function({target}) {
    return (
        <button type="button" 
                className="navbar-toggle x collapsed nav__open-btn" 
                data-bs-toggle="offcanvas" 
                data-bs-target={`#${target}`} 
                aria-controls={target}>
            <svg preserveAspectRatio="none" viewBox="0 0 1 1"></svg>
        </button>
    )
}