import React from "react";
import './InfoCardHorizontal.scss';

export const InfoCardHorizontal = function({text, img, reversed, ...props}) {
    props.className = 'info-card container-fluid ' + 
        (props.className ? props.className : '');
    

    if (reversed) {
        props.className += ' info-card__right'

        return (
            <div {...props} >
                <div className="row">
                    <div className="container col-12 order-1 col-md-6 order-md-0">
                        <p className="col-sm-12 col-md-8 offset-sm-0 offset-md-4">{text}</p>
                    </div>
                    <img className='col' src={img} />
                </div>
            </div>
        )
    }

    props.className += ' info-card__left'

    return (
        <div {...props} >
            <div className="row">
                <img className='col' src={img} />
                <div className="container col-12 order-1 col-md-6 order-md-0">
                    <p className="col-sm-12 col-md-8">{text}</p>
                </div>
            </div>
        </div>
    )
}