import React from "react";
import './PersonDescription.scss';

export const PersonDescription = function({title, text, img}) {
    return (
        <div className="person-description">
            <div className="col-8 align-self-center person-description__info">
                <p><span>{title}</span>{text}</p>
            </div>
            <img className="offset-2" src={img} />
        </div>
    );
}