import React from "react";
import './ContentContainer.scss';

export const ContentContainer = function({children, header, centerContent = true, ...props}) {
    if (centerContent) {
        props.className = `${props.className} d-flex align-items-center`;

        return (
            <section {...props}>
                <div className="container">
                    <div className="col">
                        <h2>{header}</h2>
                        {children}
                    </div>
                </div>
            </section>
        );
    }

    return (
        <section {...props}>
            <h2>{header}</h2>
            {children}
        </section>
    )
}


