import React, { useState } from "react";
import { PlacePoint } from './PlacePoint/PlacePoint.jsx';
import { AnswersPanel } from "./AnswersPanel/AnswersPanel.jsx";
import { ModalContainer } from "./ModalContainer/ModalContainer.jsx";
import './GrodnoGame.scss';

export const GrodnoGame = function({points}) {
    const [selectedAnswerModal, setSelectedAnswerModal] = useState(points[0].answersModal);
    const [shownModal, setShownModal] = useState(false);
    
    const onClick = (answersModal) => {
        setSelectedAnswerModal(answersModal);
        setShownModal(true);
    }
    const onCloseModal = () => {
        setShownModal(false);
    }

    return (
        <>
            <svg preserveAspectRatio="none" viewBox="0 0 1920 1920" version="1.1">
                {points.map(point =>
                    <PlacePoint x={point.x} y={point.y} onClick={() => onClick(point.answersModal)} />)}
            </svg>
            <ModalContainer shown={shownModal} onClose={onCloseModal}>
                    <AnswersPanel {...selectedAnswerModal}/>
            </ModalContainer>  
        </>
    )
}