import React from "react";
import './InfoCardVertical.scss'

export const InfoCardVertical = function({img, title, description, alt}) {
    return (
        <div className="info-card-vertical text-center lg-auto">
            <img src={img} className="card-img-top" alt={alt}/>
            <div className="card-body">
                <h4>{title}</h4>
                <p>{description}</p>
            </div>
        </div>
    );
}