import React, { useState } from "react";
import ReactCardFlip from 'react-card-flip';
import './CarouselFlipItem.scss';

export const CarouselFlipItem = function({frontImage, backText, isActive}) {
    const [isFlipped, setIsFlipped] = useState(false);
    const handleClick = () => {
        setIsFlipped(!isFlipped);
    }
   
    return (
        <div className={(`carousel-item ${isActive ? ' active' : ''}`)} onClick={handleClick}>
            <ReactCardFlip isFlipped={isFlipped} flipDirection='horizontal'flipSpeedFrontToBack={1.2} flipSpeedBackToFront={1.2}>
                <img src={frontImage} className='d-block w-100' />
                <div className="flip-item-back">
                    <img className="d-block w-100" src={frontImage} />
                    <p className="text-center">{backText}</p>
                </div>
            </ReactCardFlip>
        </div>
      )
}