import React from "react";
import { CarouselIndicator } from "./CarouselIndicator";

export const Carousel = function({children, id}) {
    const indicators = children.map((item, index) => 
        <CarouselIndicator target={id} index={index} key={index} isActive={!!index}/>);

    return (
        <div id={id} className="carousel slide container" data-bs-ride="carousel">
            <div className="carousel-indicators">{indicators}</div>
            <div className="carousel-inner">{children}</div>
            <button className="carousel-control-prev" type="button" data-bs-target={(`#${id}`)} data-bs-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="visually-hidden">Previous</span>
            </button>
            <button className="carousel-control-next" type="button" data-bs-target={(`#${id}`)} data-bs-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="visually-hidden">Next</span>
            </button>
        </div>
    );
}