import React from "react";
import './InfoCardGroup.scss';

export const InfoCardGroup = function({children, maxCols=3, ...args}) {
    args.className = `info-card-group row row-cols-sm-1 row-cols-md-2 row-cols-lg-${maxCols} g-4 ${args.className}`;

    return (
        <div {...args}>
            {children}
        </div>
    );
}