import React, { useState } from "react";
import { PersonLogo } from "./PersonLogo/PersonLogo.jsx";
import { PersonDescription } from "./PersonDescription/PersonDescription.jsx";

export const InfoGame = function({persons}) {
    const [selectedPerson, setSelectedPerson] = useState(persons[0]);
    const logos = persons.map(person => 
        <PersonLogo img={person.img} onClick={() => setSelectedPerson(person)} />)

    return (
        <div className="container person-game">
           <div className="row align-items-center">
                <div className="col-12 col-xl-2 m-2 m-xl-0 d-flex gap-3 justify-content-around flex-wrap">
                    {logos}
                </div>
                <div className="col-12 col-xl-10 order-first order-xl-last">
                    <PersonDescription {...selectedPerson.description}/>
                </div>
           </div>
        </div>
    )
}