import React from "react";
import './Credits.scss';

export const Credits = function({title, values, ...params}) {
    const className = params.className ?
        `credits ${params.className}` :
        'credits';

    return (
        <div className={className}>
            <h3>{title}</h3>
            {values.map((value, index) => <p key={index} >{value}</p>)}
        </div>
    )
}