import React from "react";
import './NavMenu.scss';

export const NavMenu = function({children, id}) {
    return (
        <div id={id} className="offcanvas offcanvas-start" data-bs-scroll="true" data-bs-backdrop="false" aria-labelledby={`${id}Label`}>
            <div className="offcanvas-header">
                <button type="button" className="text-reset nav__close-btn" data-bs-dismiss="offcanvas" aria-label="Close">
                    <svg preserveAspectRatio="none" viewBox="0 0 1 1"></svg>
                </button>
            </div>
            <div className="offcanvas-body">{children}</div>
        </div>
    );
}