import React from "react";
import './Interlude.scss';

export const Interlude = function({title, text, ...props}) {
    props.className = 'container interlude ' + 
        (props.className ? props.className : '');

    return (
        <div {...props}>
            {title && (
                <div className="row justify-content-center">
                    <h3 className="col-12 col-xl-8">{title}</h3>
                </div>
            )}
            {text && ( 
                <div className="row justify-content-center">
                    <p className="col-12 col-xl-8">{text}</p>
                </div>
            )}
        </div>
    )
}