import React, { useRef, useEffect, useState } from "react";
import './AnswersPanel.scss';

export const AnswersPanel = function({values, answer}) {
    const answersGroup = useRef(null);
    const answers = ObjectToAnswerPairs(values);
    const [disabled, setDisabled] = useState(false);
    const onClick = (evt) => {
        if (disabled) {
            return;
        }

        const target = evt.target;
        const userAnswer = target.dataset.index;
        const correctAnswerElement = answersGroup.current.querySelector(`[data-index='${answer}']`);

        if (userAnswer != answer) {
            target.classList.toggle('wrong');
        }

        correctAnswerElement.classList.toggle('right');
        setDisabled(true);
    };

    useEffect(() => {
    }, [answersGroup]);

    return (
        <div className="answers-group list-group" ref={answersGroup}>
            {answers.map((answer, index) => 
                <button type="button" className="list-group-item" data-index={answer.index} onClick={onClick} key={index}>
                    {answer.text}
                </button>)}
        </div>
    );
}

function ObjectToAnswerPairs(obj) {
    const pairs = [];
    for(let key in obj) {
        pairs.push(
        {
            index: key, 
            text: obj[key]
        });
    }

    return pairs;
}