import React from "react";

export const CarouselIndicator = function({target, index, isActive}) {
    return (
        <button type="button" 
            className={(isActive ? 'active' : '')}
            data-bs-target={(`#${target}`)} 
            data-bs-slide-to={index} 
            aria-current="true" 
            aria-label={(`Slide ${index}`)}>
        </button>
    );
}